import {
  AfterViewInit,
  Component,
  ComponentFactoryResolver,
  ComponentRef,
  ElementRef,
  EventEmitter,
  forwardRef,
  Injector,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Optional,
  Output,
  SimpleChanges,
  ViewChild,
  ViewContainerRef
} from "@angular/core";
import { DialogComponent } from "@farris/ui-dialog";
import { VariableParseService, ViewModel } from "@farris/devkit";
import { ExternalIntegrationService } from "./external-integration-service";
import { DatagridComponent } from "@farris/ui-datagrid";
import {
  ControlValueAccessor,
  NgControl,
  NG_VALUE_ACCESSOR
} from "@angular/forms";
import { TableColumn, TableInfo } from "./external-integration-displaytype";
import { Observable, of } from "rxjs";
import { ListViewComponent } from "@farris/ui-list-view";

@Component({
  selector: "web-external-integration",
  templateUrl: "./external-integration.component.html",
  styleUrls: ["./external-integration.component.scss"],
  providers: [
    ExternalIntegrationService,
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => ExternalIntegrationComponent),
      multi: true
    }
  ]
})
export class ExternalIntegrationComponent
  implements OnInit, OnChanges, AfterViewInit, OnDestroy, ControlValueAccessor {
  /** 模态框宽 */
  @Input() width: number = 960;
  /** 模态框高 */
  @Input() height: number = 577;
  /** 服务名 */
  @Input("serviceCode") serviceCode: string;
  /** 外部服务请求入参,表达式 */
  @Input("externalParams") externalParams: any;
  /** form:主表回填形式,grid：子表回填形式 */
  @Input("mappingType") mappingType: string;
  /** 映射关系 */
  @Input("mapFields") myField: any;
  @Input() fieldText: string = "";
  /** sap出参表信息描述 */
  @Input("extTableSchemas") extTableSchemas: any;

  @Input() placeholder = "请选择";
  // 是否只读
  @Input() readonly = false;
  // 请求前事件
  @Input() beforeSapSrvc: Observable<any>;

  /** 内容中留白边距 */
  containerMargin = { top: 0, bottom: 5, left: 10, right: 10 };
  /** 自定义数据 */
  @Input() customData: any;
  /** 指令回填sap数据 */
  @Output() selectSapData = new EventEmitter();
  @Output() afterOnblur = new EventEmitter();
  @ViewChild("dialog") dialog: DialogComponent;
  @ViewChild("input") input: ElementRef<any>;
  @ViewChild("girdView", { read: ViewContainerRef }) girdView: ViewContainerRef;
  @ViewChild("farrisListView") farrisListView: ListViewComponent;
  componentRef: ComponentRef<DatagridComponent>;

  /** 多表数据集合 */
  tableData: TableInfo[] = [];
  /** 模态框导航栏当前选中项 */
  currentLink: any = {};
  /** 模态框确定按钮是否可用 */
  ensureFlag: boolean = true;

  ngControl: NgControl;

  containerStyle = {
    marginLeft: this.containerMargin.left + "px",
    marginRight: this.containerMargin.right + "px",
    marginTop: this.containerMargin.top + "px",
    marginBottom: this.containerMargin.bottom + "px"
  };

  constructor(
    @Optional() private vm: ViewModel,
    public el: ElementRef,
    private extIntgrtnSrvc: ExternalIntegrationService,
    private variableParseService: VariableParseService,
    private resolver: ComponentFactoryResolver,
    private inj: Injector
  ) {}

  ngOnChanges(changes: SimpleChanges): void {}

  fieldTextChange() {
    const pathArr = this.getBindingPathArray();
    this.vm.bindingData.setValue(
      pathArr.concat(this.ngControl.name),
      this.fieldText,
      true,
      true
    );
  }

  ngAfterViewInit(): void {}
  ngOnDestroy(): void {}
  writeValue(obj: any): void {
    this.fieldText = obj;
  }
  registerOnChange(fn: any): void {}
  registerOnTouched(fn: any): void {}
  setDisabledState?(isDisabled: boolean): void {}

  ngOnInit() {
    this.ngControl = this.inj.get(NgControl);
    // 卡片绑定onblur事件，表格再grid-external-integration中绑定onblur事件
    if (this.mappingType != "grid") {
      this.input.nativeElement.onblur = () => {
        this.callSapService();
      };
    }

    this.extIntgrtnSrvc.sapData.subscribe((data: any) => {
      this.sapDataHandle(data);
    });
  }

  /**
   * 打开模态框
   * @returns
   */
  showDialog(): any {
    this.farrisListView.listClick.emit({
      data: [this.tableData[0]],
      index: 0,
      checkChangeEvent: false
    });
    this.farrisListView.clickItem = this.tableData[0];
    this.dialog.show();
    return false;
  }

  /**
   * 调用sap外部服务，出参在ngOninit监听
   */
  callSapService() {
    this.fieldTextChange();
    this.extIntgrtnSrvc.queryExtIntegrationService(
      this.externalParams,
      this.serviceCode
    );
  }

  /**
   * 处理sap出参
   * @param data sap出参
   */
  sapDataHandle(data) {
    if (!data) {
      return;
    }
    let tableNameList = Object.keys(data);
    let mapTableList = this.getTableListFromMapFields();
    // 取交集
    tableNameList = tableNameList.filter(item => {
      return mapTableList.indexOf(item) != -1;
    });
    // 多表场景
    if (tableNameList.length > 1) {
      // 校验是否弹出模态框
      let openDialogFlag = this.checkMultiTableData(data, tableNameList);
      if (openDialogFlag == "open") {
        // 组装模态框数据
        this.assembleTableData(data);
        this.showDialog();
      } else if (openDialogFlag == "backfill") {
        // 多表，每个表只有一条数据
        let backfillData: any = {};
        tableNameList.forEach(tableName => {
          Object.assign(
            backfillData,
            this.assembleSapData(tableName, data[tableName][0])
          );
        });
        this.sendSapData(backfillData);
      } else {
        // 多表无数据
        this.sendSapData();
      }
    } else if (tableNameList.length == 1) {
      let tableName = tableNameList[0];
      let sapDataLength = (data[tableName] || []).length;
      if (sapDataLength > 0) {
        if (sapDataLength == 1) {
          // 一个表只有一条数据
          let sapData = this.assembleSapData(tableName, data[tableName][0]);
          this.sendSapData(sapData);
        } else if (sapDataLength > 1) {
          // 一个表有多条数据
          this.assembleTableData(data);
          this.showDialog();
          return;
        }
      }
    } else {
      // 处理查询异常，editor没有endPending
      this.sendSapData();
    }
  }

  checkMultiTableData(data, tableNameList) {
    let openDialogFlag = "";
    for (let index = 0; index < tableNameList.length; index++) {
      let tableName = tableNameList[index];
      if (data[tableName].length > 1) {
        openDialogFlag = "open";
        break;
      } else if (data[tableName].length == 0 && openDialogFlag != "backfill") {
        openDialogFlag = "none";
        continue;
      }
      openDialogFlag = "backfill";
    }
    return openDialogFlag;
  }

  /**
   * 组装模态框数据
   * @param data sap出参
   */
  assembleTableData(data) {
    let tableNameList = Object.keys(data);
    let mapTableList = this.getTableListFromMapFields();
    // 取交集
    mapTableList = mapTableList.filter(item => {
      return tableNameList.indexOf(item) != -1;
    });
    if (mapTableList.length > 0) {
      // 遍历sap映射表，组装表及对应数据
      mapTableList.forEach(mapTable => {
        // 组装模态框中单表数据
        let tableInfo = this.assembleTableInfo(mapTable, data);
        if (
          tableInfo &&
          Object.keys(tableInfo).length > 0 &&
          (tableInfo.data || []).length > 0
        ) {
          this.tableData.push(tableInfo);
        }
      });
    }
  }

  getTableListFromMapFields() {
    // 获取需要回填的表
    let mapKeyList = Object.keys(this.myField);
    // 定义sap映射表集合
    let mapTableList = [];
    (mapKeyList || []).forEach(mapKey => {
      // 在映射关系中获取sap出参中的表集合
      mapTableList.push(mapKey.split(".")[0]);
    });
    // 去重
    mapTableList = Array.from(new Set(mapTableList));
    return mapTableList;
  }

  /**
   * 组装模态框中单表数据
   * @param mapTable 表名
   * @param data sap出参
   * @returns 模态框单表数据对象
   */
  assembleTableInfo(mapTable, data) {
    // 模态框单表数据对象
    let tableInfo: TableInfo = {

    };
    // 表头集合
    let tableColumnList = [];
    // 遍历sap出参表信息，获取表头中文、表名
    (this.extTableSchemas || []).forEach(tableSchema => {
      if (tableSchema.code == mapTable) {
        tableInfo.name = tableSchema.name;
        // 模拟主键，保证farris-grid组件【单选】功能正常
        let idColumn: TableColumn = {};
        idColumn.field = "id";
        idColumn.title = "序号";
        tableColumnList.push(idColumn);
        // 组装表头
        tableSchema.columns.forEach(column => {
          let tableColumn: TableColumn = {};
          tableColumn.field = column.code;
          tableColumn.title = column.name;
          tableColumnList.push(tableColumn);
        });
        tableInfo.columns = tableColumnList;
        tableInfo.code = mapTable;
        // 给表数据增加模拟主键
        (data[mapTable] || []).forEach((item, index) => {
          item["id"] = index + 1;
        });
        tableInfo.data = data[mapTable];
      }
    });
    return tableInfo;
  }

  /**
   * 导航栏点击事件
   * @param event 导航栏信息
   * @returns
   */
  listClick(event) {
    let clickData = event.data;
    if (event.data.disable) {
      return false;
    } else {
      if (typeof this.currentLink["data"] !== "undefined") {
        if (clickData[0].code === this.currentLink["data"][0].code) {
          return false;
        } else {
          this.currentLink = event;
        }
      } else {
        this.currentLink = event;
      }
    }
  }

  /**
   * 模态框，确定，取消按钮
   * @param flag ensure:确定按钮，cancel：取消按钮
   */

  confirm(flag) {
    // 点击确定按钮
    if (flag == "ensure") {
      let sapData: any = {};
      // 遍历模态框表数据，组装回填数据
      for (let index = 0; index < this.tableData.length; index++) {
        let tableInfo = this.tableData[index];
        Object.assign(
          sapData,
          this.assembleSapData(tableInfo.code, tableInfo.selectedData.data)
        );
      }
      this.sendSapData(sapData);
    }
    // 清空模态框数据
    this.tableData = [];
    // 关闭模态框
    this.dialog.close();
  }

  beforeClose = () => {
    this.tableData = [];
    this.ensureFlag = true;
    this.sendSapData();
    return of(true);
  };

  /**
   * 选中一行事件
   * @param selectedRow 选中行数据
   * @param tableName 当前表格对应表名
   */
  selectRowChange(selectedRow, tableName) {
    this.tableData.forEach(tableInfo => {
      if (tableInfo.code == tableName) {
        tableInfo.selectedData = selectedRow;
      }
    });
    this.setEnsureButtonFlag();
  }

  /**
   * 取消一行事件
   * @param selectedRow 被取消行数据
   * @param tableName 当前表格对应表名
   */
  unSelect(selectedRow, tableName) {
    this.tableData.forEach(tableInfo => {
      if (tableInfo.code == tableName) {
        tableInfo.selectedData = null;
        tableInfo.selectedValue = "";
      }
    });
    selectedRow = null;
    this.setEnsureButtonFlag();
  }

  /**
   * 设置模态框确定按钮是否可用
   */
  setEnsureButtonFlag() {
    let flag = false;
    for (let index = 0; index < this.tableData.length; index++) {
      let tableInfo = this.tableData[index];
      if (!tableInfo.selectedData) {
        flag = true;
        break;
      }
    }
    this.ensureFlag = flag;
  }

  /**
   * 组装sap回填数据
   * @param tableName 表名
   * @param tableData 表数据
   * @returns (表名.表字段名:表数据):Object
   */
  assembleSapData(tableName, tableData) {
    if (!tableName || !tableData) {
      return;
    }
    // sap回填数据
    let sapData: any = {};
    // 表字段名集合
    let tableDataItemKeys = Object.keys(tableData);
    for (let index = 0; index < tableDataItemKeys.length; index++) {
      let itemKey = tableDataItemKeys[index];
      // sap回填数据key= 表名.表字段名
      let key = tableName + "." + itemKey;
      sapData[key] = tableData[itemKey];
    }
    return sapData;
  }

  /**
   * 回填sap数据
   * @param sapData 将要回填的sap数据
   */
  private sendSapData(sapData?) {
    if (sapData) {
      this.selectSapData.emit(sapData);
    }
    // 子表关闭editor状态
    if (this.mappingType == "grid") {
      this.afterOnblur.emit();
    }
  }

  private getBindingPathArray(): any[] {
    const path = this.vm.bindingPath;
    if (path) {
      return path.split("/").filter(n => n !== "");
    }
    return [];
  }
}
