import {
  Directive,
  EventEmitter,
  Input,
  Optional,
  Output
} from "@angular/core";
import {  ViewModel } from "@farris/devkit";
import { ExternalIntegrationComponent } from "../../lib/component/external-integration/external-integration.component";
@Directive({
  selector: "[extIntegration-dataMapping]",
  
})
export class ExtIntegrationDataMappingDirective {
  @Input("mapFields") myField: any;
  @Output() showModal = new EventEmitter();
  constructor(
    @Optional() private vm: ViewModel,
    private extIntegrationComponent: ExternalIntegrationComponent
  ) {}

  ngOnInit() {
    this.extIntegrationComponent.selectSapData.subscribe((data:any) => {
      this.mappingData(data, this.myField);
    })
  }

  
  /**
   * 字段映射
   * @param sapData sap返回信息
   * @param mappingField 映射关系
   */
  mappingData(sapData, mappingField) {
    let helpFields = Object.keys(mappingField);
    helpFields.forEach((fieldName: any) => {
      let pathArr = this.getBindingPathArray();
      // 获取字段值
      let val: any;
      if (sapData) {
        val = sapData[fieldName];
      }
      this.vm.bindingData.setValue(
        pathArr.concat(mappingField[fieldName]),
        val,
        true,
        true
      );
    });
  }

  private getBindingPathArray(): any[] {
    const path = this.vm.bindingPath;
    if (path) {
      return path.split("/").filter((n) => n !== "");
    }
    return [];
  }

  
}
